$module-title: 'gallery';

html, body {
	height: 100%;
}

body,
.#{$module-title}__desktop {
	max-width: 100%;
	max-height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
	font-family: 'Saira', sans-serif;
}

select,
option {
	font-family: 'Saira', sans-serif;
	font-size: 15px;
}

h1 {
	margin-top: 0;
}

p {
	margin-bottom: 0;
}

##{$module-title}__map {
	height: 100%;
	width: 100%;
}

.#{$module-title} {
	&--hidden {
		display: none !important;
	}

	&--fadeout {
		opacity: 0 !important;
		color: transparent;
		background-image: none;
		transform: scale(0);
	}

	&--visible {
		opacity: 1;
	}

	&__desktop {
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		margin: auto;
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__window {
		z-index: 1;
		max-width: 90%;
		max-height: 90%;
		margin: auto;
		padding: 0;
		opacity: 1;
		height: auto;
		width: auto;
		transition: all 2s;
		position: relative
	}

	&__content {
		padding: 15px;
		background-color: #ffffff;
		border-radius: 15px;
		overflow: hidden;
		box-shadow: 0 0 15px #000;
		pointer-events: auto;
		opacity: 1;
		transition: all 2s;
	}

	&__image {
		display: block;
		margin: auto;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: all 2s;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    &.#{$module-title}--visible {
			opacity: 1;
		}

		&-container {
			padding: 0;
			background-color: rgba(0, 0, 0, .5);
		  margin: auto;
		  width: 100%;
		  height: 100%;
		  max-width: 100%;
		  max-height: 100%;
	    background-repeat: no-repeat;
	    background-size: 100% 100%;

	    .wait-icon {
				position: absolute;
				top: calc(50% - 50px);
				left: calc(50% - 50px);
				width: 100px;
				height: 100px;
				opacity: 1;
				transition: opacity .5s;
	    }

			span {
				height: 100%;
				width: 100%;
			}
		}

		&-caption {
			background-color: #ffffff;
			display: block;
			z-index: 2;
			width: 60%;
			position: absolute;
			margin: auto 10%;
			border-radius: 15px;
			padding: .5em 10%;
			text-align: center;
			box-shadow: 0 0 15px #000;
			bottom: -2em;

			&-text {
				margin: auto;
			}
		}
	}

	&__control {
		min-height: 45px;
		opacity: 1;
		transition: opacity 2s;
	}

	&__button {
		background-color: #ffffff;
		width: 45px;
		height: 45px;
		border-radius: 50%;
		box-shadow: 0 0 15px #000;
		background-size: 50%;
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
		display: block;
		position: absolute;
		z-index: 1;
		pointer-events: auto;
		color: transparent;

		&:hover {
			box-shadow: 0 0 5px #000;
    	padding-bottom: 2px;
		}

		&--fullscreen,
		&--windowed {
		    align-self: flex-start;
		    left: -22px;
		    top: -22px;
		}

		&--google-plus {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--mail {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--facebook {
			background-image: url(data:image/svg+xml;base64,);
			background-size: 25%;
		}

		&--twitter {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--fullscreen {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--windowed {
			background-image: url(data:image/svg+xml;base64,);
		}


		&--delete,
		&--rotate,
		&--mute,
		&--unmute {
			top: 44px;
			transform: translate(-50%, -50%);
		}

		&--delete {
			background-image: url(data:image/svg+xml;base64,);
			left: 50%;
	  }

		&--rotate {
			background-image: url(data:image/svg+xml;base64,);
			left: calc(50% + 53px);
	  }

		&--mute {
			background-image: url(data:image/svg+xml;base64,);
			left: calc(50% - 53px);
	  }

		&--unmute {
			background-image: url(data:image/svg+xml;base64,);
			left: calc(50% - 53px);
	  }

		&--close {
			background-image: url(data:image/svg+xml;base64,);
			right: -22px;
			top: -22px;
		}

		&--prev {
			transform: rotateZ(180deg);
			align-self: flex-start;
			left: -22px;
		}

		&--next {
			align-self: flex-end;
			right: -22px;
		}

		&--cleanup {
			margin-top: 7px;
			width: auto;
			padding: 7px;
			min-height: 0;
			height: auto;
			border-radius: 5px;
			margin-bottom: 5px;
			box-shadow: 0 0 5px #000;

			&:hover {
				padding: 7px;
				box-shadow: 0 0 1px #000;
			}

			&[disabled=disabled] {
				box-shadow: none;
				border: 1px solid #666666;
				background-color: rgba(0, 0, 0, 0.2);
			}
		}

		.#{$module-title}__navigation & {
			position: relative;
    	color: #000000;
		}

		.#{$module-title}__image-caption & {
			background-image: url(data:image/svg+xml;base64,);
			background-position: 15px center;
			position: absolute;
			bottom: 10px;
		}
	}

	&__share {
		width: auto;
		margin: auto;

		&-wrapper {
			width: 100%;
			position: absolute;
			top: -22px;
			margin: 0;
			display: flex;

			.#{$module-title}__button {
				position: relative;
				margin: 0 2px;
				display: inline-block;

				&:hover {
		    	margin-top: -2px;
				}
			}
		}
	}

	&--fullscreen {
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;
		margin: 0;
		padding: 0;

		.#{$module-title}__button--close,
		.#{$module-title}__button--fullscreen,
		.#{$module-title}__button--windowed {
			top: 22px;
		}

		.#{$module-title}__button--fullscreen,
		.#{$module-title}__button--windowed {
			left: 22px;
			margin-left: 0;
		}

		.#{$module-title}__button--close {
			right: 22px;
			margin-right: 0;
		}

		.#{$module-title}__button--delete {
			top: 86px;
		}

		.#{$module-title}__image-container {
			border-radius: 0;
			background-color: #000000;
			height: 100%;
			width: 100%;
		}

		.#{$module-title}__image-caption {
			bottom: 30px;
		}

		.#{$module-title}__share-wrapper {
			top: 20px;
		}
	}

	&__navigation {
		width: 150px;
		position: absolute;
		top: 80px;
		left: 22px;
		background-color: #ffffff;
		border-radius: 15px;
		padding: 7px;
		text-align: center;
		box-shadow: 0 0 15px #000;
		pointer-events: auto;
		opacity: 1;
		transition: all 2s;
	}

	&__select {
		width: 150px;
		margin-top: .5em;
	}
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wait-icon {
	height: 22px;
	width: 22px;
	display: block;
	float: left;
	background-size: contain;
	animation: rotating .5s linear infinite;
	background-image: url(data:image/svg+xml;base64,);
}

.debug--small {
	font-size: 8pt;
	display: block;
}

.background-video {
	position: absolute;
	z-index: -1;
	min-width: 100%;
	min-height: 100%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.comingsoon {
	height: 100%;
	display: flex;
	text-align: center;
	justify-content: center;
	align-items: center;
  color: #ff9900;
  font-weight: bold;
  text-shadow: 0 0 25px #000;
  animation: pusate 1s ease infinite alternate;
}

@keyframes pusate {
    from { font-size: 68px; }
    to { font-size: 72px; }
}
